import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import { Heading  } from "carbon-components-react";

const dispatchedEvents = [];

/*
    Example: <srs-heading level="1" content="Hello World"></srs-heading>
*/
const SrsHeading = function (props, ref) {
    return (
      <Heading level={props.level}>{props.content}</Heading>
    );
}

const styles = `
    
`
const props = [{
    name: "level",
    defaultValue: 1,
    type: "number"
}, {
    name: "content",
    defaultValue: "",
    type: "string"
}]

const slots = [];

const WrappedComponent = wrapper(React.forwardRef(SrsHeading), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };